import { Component } from 'react'
import img1 from '../assets/img/avatar-kady.jpg'
import img2 from '../assets/img/avatar-aiysha.jpg'
import img3 from '../assets/img/avatar-arthur.jpg'


import { ReactComponent as PatternOne } from "../assets/icons/bg-pattern-home-4-about-3.svg"
import { ReactComponent as PatternTwo } from "../assets/icons/bg-pattern-home-5.svg"
import { ReactComponent as PatternThree } from "../assets/icons/icon-quotes.svg"



export class AppCreators extends Component {


  render() {

    return (
      <div className="app-creators">
        <div className="pattern-one"><PatternOne /></div>
        <div className="pattern-two"><PatternTwo /></div>
        <div className="content container">
          <h1>Delivering real results for top companies. <br /> Some of our <span>success stories</span> </h1>
          <div className="box-holder flex justify-between">

            <div className="box">

              <div className="txt-holder">
                <div className="pattern-three"><PatternThree /></div>
                <div className="txt">
                  <p> “The team perfectly fit the specialized skill set <br /> required. They focused on the most essential <br /> features helping us launch the platform eight <br /> months faster than planned.”</p>
                </div>
              </div>
              <h1>Kady Baker</h1>
              <h2>Product Manager at Bookmark</h2>

              <div className="img-holder flex auto-center">
                <img src={img1} alt="" />
              </div>

            </div>

            <div className="box">

              <div className="txt-holder">
                <div className="pattern-three"><PatternThree /></div>
                <div className="txt">
                  <p> “We needed to automate our entire onboarding <br/> process. The team came in and built out the whole <br/> journey. Since going live, user retention has gone <br/> through the roof!”</p>
                </div>
              </div>

              <h1>Aiysha Reese</h1>
              <h2>Founder of Manage</h2>

              <div className="img-holder flex auto-center">
                <img src={img2} alt="" />
              </div>

            </div>

            <div className="box">

              <div className="txt-holder">

                <div className="pattern-three"><PatternThree /></div>
                <div className="txt">
                  <p>“Amazing. Our team helped us build an app that <br/> delivered a new experience for hiring a physio. The <br/> launch was an instant success with 100k <br/> downloads in the first month.”</p>
                </div>
              </div>

              <h1>Arthur Clarke</h1>
              <h2>Co-founder of MyPhysio</h2>

              <div className="img-holder flex auto-center">
                <img src={img3} alt="" />
              </div>

            </div>

          </div>


        </div>



      </div>
    )
  }
}
