import { Component } from 'react'


import { ReactComponent as PatternOne } from "../assets/icons/bg-pattern-home-1.svg"
import { ReactComponent as PatternTwo } from "../assets/icons/bg-pattern-home-2.svg"


export class AppHero extends Component {


    render() {

        return (
            <div className="app-hero">
                <div className="patttern-one"><PatternOne /></div>

                <div className="content container flex justify-between">
                    <h1>Find the <br /> best <span>talent</span> </h1>
                    <div className="flex column justify-between">
                        <div className="pattern"></div>
                        <p>Finding the right people and building high performing <br />
                        teams can be hard. Most companies aren’t tapping into <br />
                        the abundance of global talent. We’re about to change <br />
                        that.</p>
                        <div className="patttern-two"><PatternTwo /></div>
                    </div>
                    
                </div>

            </div>
        )
    }
}
