import { AppGeogram } from './pages/AppGeogram'
import { Map } from './pages/Map'
import { Home } from './pages/Home'


export const routes = [
    {
        path: '/geogram',
        component: AppGeogram
    },
    {
        path: '/map',
        component: Map
    },
    
    {
        path: '/',
        component: Home
    }
]