import { Component } from 'react'
import { ReactComponent as Logo } from "../assets/icons/logo.svg"

import { ReactComponent as Facebook } from "../assets/icons/icon-facebook.svg"
import { ReactComponent as Linkedin } from "../assets/icons/icon-linkedin.svg"
import { ReactComponent as Twitter } from "../assets/icons/icon-twitter.svg"


export class AppFooter extends Component {


    render() {

        return (
            <div className="app-footer">
                <div className="content container flex justify-between">

                    <div>
                        <div className="logo"><Logo /></div>
                        <div className="flex justify-between">
                            <p>home</p>
                            <p>about</p>
                        </div>
                    </div>

                    <div className="box-one">
                        987  Hillcrest Lane <br />
                        Irvine, CA <br />
                        California 92714 <br />
                        Call Us : 949-833-7432
                    </div>

                    <div className="box-two  flex column justify-between ">

                        <div className="box-two-content flex row-reverse">
                            <div className="social flex auto-center "><Twitter /></div>
                            <div className="social flex auto-center "><Linkedin /></div>
                            <div className="social flex auto-center "><Facebook /></div>
                        </div>

                        <p>Copyright 2020. All Rights Reserved</p>

                    </div>

                </div>


            </div>
        )
    }
}
